import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { toJS } from 'mobx'

import Map from '../../components/map';

import mu from '../../lib/musher-util';

const initialState = {
	showAddComment: false,
};

@observer
class Trackmap extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
		this.inputRef = {};
    }

	async loadTrack(props = this.props) {
        const { showHeatmap, raceid, allTeamIds } = props;
        const { adminStore, trackStore, raceStore, appState } = props.stores;
        const { selectedClassIds = [] } = appState;

		// this.setState({ isLoading: true });
        if (showHeatmap) {
            await adminStore.getPublicHeatmap(raceid, allTeamIds);
        }

        // const classId = 
        const trackIds = raceStore.getTrackIdsByClass(selectedClassIds[0]);

        if (trackIds && trackIds.length > 0) {
            trackStore.cleanupMemory();
            await trackStore.loadPublic({ query: { idin: trackIds } });
        }
		// this.setState({ isLoading: false });
	}

	componentDidMount() {
		this.loadTrack();
	}

    componentWillReceiveProps(nextProps) {
        if (this.props.selectedClassIds !== nextProps.selectedClassIds) {
            this.loadTrack(nextProps);
        }
	}

    render() {
        const { trackStore, raceStore, appState, adminStore } = this.props.stores;
        const { selectedClassIds = [] } = appState;
        const {
            waypoints, waypoints2, waypoints3, zoomToMarkers, mapid,
            fullscreenMap, onMarkerClick, showHeatmap, showTracks,
            showDistanceMarkers, groupWaypoints,
            focusOnMushers, geoJSONs,
        } = this.props;
        const { tracks } = trackStore;
        const { publicHeatmap: heatmap } = adminStore;
        const track = tracks[0];
        const trackIds = raceStore.getTrackIdsByClass();

        // const heatmap = publicHeatmap.slice(0, 10);
        // const heatmap = [[60.4433,8.8996,14],[61.384,9.1152,14],[60.42,8.9422,15]];

        return (
            <>
                {/* heatmap: {`map-${mapid}`}<br /> */}
                {/* {JSON.stringify(showTracks && track && track.gpx ? track.gpx.geoJSON : null)} */}
                {/* {JSON.stringify(geoJSONs)} */}
                <Map
                    stores={this.props.stores}
                    // mapId={mapid}
                    heatmap={showHeatmap ? heatmap : null}
                    track={track}
                    geojson={showTracks && track && track.gpx ? track.gpx.geoJSON : null}
                    gpxInfo={showTracks && track && (track.gpx || track.gpxInfo) ? (track.gpxInfo || track.gpx.gpxInfo) : null}
                    minZoom={2}
                    maxZoom={18}
                    height={600}
                    waypoints={waypoints}
                    waypoints2={waypoints2}
                    waypoints3={waypoints3}
                    geoJSONs={geoJSONs}
                    zoomToMarkers={zoomToMarkers}
                    fullscreenMap={fullscreenMap}
                    onMarkerClick={onMarkerClick}
                    showTrackProfile={false}
                    distanceMarkerSteps={'long'}
                    showDistanceMarkers={showDistanceMarkers}
                    groupWaypoints={groupWaypoints}
                    focusOnMushers={focusOnMushers}
                    showFullscreenControl={false}
                    // key={`map-${mapid}-${util.getNestedValue(track, 'id')}-${trackIds.join('-')}-${selectedClassIds.join('-')}`}
                    // key={`map-${util.getNestedValue(track, 'id')}`}
                />
            </>
        );
    }
}

export default Trackmap;
