import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

@observer
class Drawer extends Component {

    render() {
        const { appState } = this.props.stores;
        const { darkmode } = appState;
        const { children, onCloseClick = () => {} } = this.props;
        return (
            <div
                class={`fixed-bottom ${darkmode ? 'bg-darkmode' : '.bg-lightmode'} border rounded rounded-lg pt-4 pb-3 px-4`}
                style='
                    bottom: 0;
                    left: 0;
                    width: 100vw;
                    max-height: 80vh;
                    min-height: 10vh;
                    overflow: auto;
                    z-index: 999999;
                    border-radius: 20px 20px 0 0 !important;
                '
                onTouchmove={mu.captureEvents}
                onScroll={mu.captureEvents}
            >
                <button
                    class='btn btn-warning btn-sm float-right' style='top: 5px; right: 5px;'
                    onClick={onCloseClick}
                >
                    <i class='fas fa-times' />
                </button>

                {children}

            </div>
        );
    }
}

export default Drawer;
