    savePreCheck = async (e) => {
        const { id, chipid, name, shortname, team } = e.target.closest('button').dataset;
        const { preCheck, dogJournals } = this.state;
        const { dogJournalStore, userStore } = this.props.stores;
		const { user = {} } = userStore;

        const vetData = {
            id: user.id,
            firstname: user.firstname,
            lastname: user.lastname,
            email: user.email,
            image: user.image,
        };

        const dogData = {
            id,
            chipId: chipid,
            name,
            shortname,
            team,
        };

        const preCheckOject = util.getNestedValue(preCheck, id) || {};
        const journalData = {
            checkType: 'preCheck',
            ...preCheckOject,
        };

        const data = [{
            dog: { ...dogData },
            journal: { ...journalData },
            veterinary: { ...vetData },
        }];

        if (!dogJournals[chipid]){
            dogJournals[chipid] = [];
        }
        dogJournals[chipid].push({
            date: new Date(),
            ...dogData,
            ...journalData,
            ...vetData,
        });

        await dogJournalStore.insert({ data });
        delete preCheck[id];
        this.setState({ preCheck });
    }

