import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { route } from 'preact-router';
import Markdown from 'preact-markdown';
import linkstate from 'linkstate';

import RaceComments from '../public/race/comments';
import RaceAddComment from '../public/race/commentAdd';
import RaceText from '../public/race/raceText';
import Images from '../../components/form/images';
import LikesAndComments from '../../components/feedback/likesAndComments';

import ScrollIntoView from '../../components/util/scrollIntoView';

import RaceResults from './results';
import RaceNews from './news';
import RaceSignup from './signup';
import RaceClasses from './classes';
import RaceMushers from './mushers';
import RaceUserImages from './userImages';
import RaceTracking from './tracking';
import RaceNotifications from './notifications';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

const MARKDOWN_OPTIONS = {
	pedantic: false,
	gfm: true,
	breaks: true,
	sanitize: false,
	smartLists: true,
	smartypants: true,
	xhtml: true,
};

const initialState = {
	isLoading: false,
    showAdminSendMessage: {},
	newRaceArticle: {},
	newRaceUserImage: {},
	message: {
		subject: '',
		body: '',
	},
};

@observer
class AppRaceDetailSignup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    async loadRace(props = this.props) {
		this.setState({ isLoading: true });
		const { raceStore } = props.stores;
		const query = {
            // relevant: 1,
        };
		await raceStore.load({ query });
		this.setState({ isLoading: false });
	}

    scrollRaces = (e) => {
        // scrollHeight: 705
        // scrollLeft: 564
        // scrollTop: 0
        // scrollWidth: 1692

        const { scrollLeft, scrollWidth, clientWidth } = e.target;
        const step = clientWidth;
        const raceIdx = scrollLeft / step;

        const nearestInt = Math.round(raceIdx);
        const diff = Math.abs(nearestInt - raceIdx);
        // console.log({ e, step, raceIdx, scrollLeft, scrollWidth, clientWidth, nearestInt, diff });
        // console.log({ nearestInt, diff });
        if (Number.isInteger(raceIdx) || diff < 0.5) {
            this.setState({
                raceIdx: nearestInt,
            });
        }
    }

    componentDidMount() {
		this.loadRace();
	}

    back = () => {
        route(`/`);
    }

	render() {
		const { userStore, raceStore, newsStore, appState } = this.props.stores;
		const { user = {}, teams = [], isAdmin } = userStore;
        const { language = 'en' } = user;

        const { races = [] } = raceStore;
		const { newsList } = newsStore;
		const news = newsList && newsList[0] ? newsList[0] : null;
		const { darkmode } = appState;
		const { showNews, hideRaces, raceIdx } = this.state;

        const today = new Date();

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <button class='btn btn-link pl-0 fixed-top text-primary'
                    style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                    onClick={this.back}>
                    <i class='fas fa-arrow-circle-left' />
                </button>

                <div class='row m-0'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-1 mb-2 p-0 text-center'>
                        <h5><Text id='races.all-races'>All Races</Text></h5>
                    </div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-1 mb-2 p-0'>
                        <div class='row'>
                            {races && races.reverse().map((race, idx) =>  {
                                let status = '';
                                if (today >= new Date(race.startDate) && today <= new Date(race.endDate)) {
                                    status = 'race';
                                } else if (today >= new Date(race.signupStartDate) && today <= new Date(race.signupEndDate)) {
                                    status = 'signup';
                                } else if (today > new Date(race.endDate)) {
                                    status = 'done';
                                }
                                return (<>
                                    <div
                                        class={`col-12 mb-5 bg-${darkmode ? 'black' : 'white'} rounded-lg position-relative`}
                                        style={`
                                            scroll-snap-align: center;
                                            flex-wrap: wrap;
                                            overflow-y: hidden;
                                        `}
                                        onTouchstart={(e) => { e.stopPropagation(); }}
                                        onTouchend={(e) => { e.stopPropagation(); }}
                                        onTouchmove={(e) => { e.stopPropagation(); }}
                                    >
                                        <div class='row d-flex align-items-center position-relative'>
                                            <h5 class='position-absolute' style='top: 3px; right: 5px;'>
                                                {status === 'signup' && !race.liveRace && <>
                                                    <span class='badge badge-primary border border-white ml-2'><Text id='races.signup-open'>Pmelding pen</Text></span>
                                                </>}
                                                {status === 'race' && <>
                                                    <span class='badge badge-danger border border-white ml-2'><Text id='races.race-in-progress'>Lp pgr</Text></span>
                                                </>}
                                                {status === 'done' && <>
                                                    <span class='badge badge-success border border-white ml-2'><Text id='races.race-finished'>Lp avsluttet</Text></span>
                                                </>}
                                            </h5>
                                            {(race.images && race.images[0]) && <div
                                                class={`w-100`}
                                                style={`background-image: url("${race.images[0].s3LargeLink}"); background-size: cover; height: 200px; background-repeat: no-repeat; background-position: 50% 50%;`}
                                            />}
                                        </div>
                                        <div class='position-relative'>
                                            <h5 class='position-absolute' style='top: 10px; right: 10px;'>
                                                {status === 'signup' && <>
                                                    <div class='spinner-grow text-primary' role='status' />
                                                </>}
                                                {status === 'race' && <>
                                                    <div class='spinner-grow text-danger' role='status' />
                                                </>}
                                                {status === 'done' && <>
                                                    <span><i class='fas fa-check text-success' /></span>
                                                </>}
                                            </h5>
                                            <h5 class='pt-2'>
                                                {isAdmin && <i class={`fas fa-eye${race.active ? ' text-success' : '-slash text-danger'} float-right`} />}
                                                {race.title}
                                            </h5>
                                        </div>
                                        <div class='w-100'>
                                            <Markdown markdown={mu.replaceImages(race.ingress, race.images, language)} markdownOpts={MARKDOWN_OPTIONS} />
                                        </div>
                                        <div class='w-100'>
                                            {race.classes && <>
                                                <div class='mb-3'>
                                                    <small class='font-weight-lighter mr-2'><Text id='races.classes'>Klasser</Text>: </small><br />

                                                    <div class='w-100 position-relative'>
                                                        <div
                                                            class='d-flex flex-row flex-nowrap'
                                                            style='overflow: auto; scroll-snap-type: x mandatory;'
                                                        >
                                                            {race.classes.sort((a, b) => a.distance - b.distance).map(currentClass => <>
                                                                <span class='badge badge-pill badge-light mr-2 font-weight-light'>
                                                                    {currentClass.title}
                                                                    {/* <span class='badge badge-info ml-2 font-weight-light'>{currentClass.contestants.length}</span> */}
                                                                </span>
                                                            </>)}
                                                        </div>
                                                    </div>
                                                </div>
                                            </>}
                                        </div>
                                        <div class='w-100 d-flex justify-content-end'>
                                            <a href={`/app-races/${race.id}`} class='btn btn-primary stretched-link'>
                                                <Text id='races.read-more'>Les mer</Text>
                                                <i class='fas fa-angle-double-right ml-2' />
                                            </a>
                                        </div>
                                        <div class='w-100 d-flex justify-content-center'>
                                            <small class='text-muted'>
                                                <Text id='races.race-days'>Lpsdager</Text>: {race.startDate && <>{util.formatDate(race.startDate, { locale: language })}</>}
                                                {race.endDate && <> - {util.formatDate(race.endDate, { locale: language })}</>}
                                            </small>
                                        </div>
                                    </div>
                                </>);
                            })}
                        </div>
                    </div>
                </div>



            </div>
		);
	}
}

export default AppRaceDetailSignup;
